.main-nav {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 auto 260px;
  padding-left: 4px;

  @media (min-width: $mobile-width) and (max-width: $tablet-max-width) {
    order: 2;

    &--closed {
      display: none;
    }

    &--opened {
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      padding: 0;
      z-index: 100;
    }

    &--nojs {
      display: block;
      width: 100%;
      margin: 0 0 30px;
      padding: 0;
    }
  }
}

.main-nav__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 1009px;
  margin: 0 -68px 0 -67px;
  padding: 0 0 0 17px;
  list-style: none;
  box-sizing: border-box;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 66px;
    width: 874px;
    height: 1px;
    background-color: $basic__white;
    opacity: 0.2;
  }

  @media (min-width: $mobile-width) and (max-width: $tablet-max-width) {
    flex-wrap: nowrap;
    flex-direction: column;
    width: auto;
    min-width: 320px;
    height: 100vh;
    margin: 0;
    padding: 100px 0;
    background-color: $basic__white;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;

    &::after {
      display: none;
    }
  }
}

.main-nav--nojs .main-nav__list {
  @media (min-width: $mobile-width) and (max-width: $tablet-max-width) {
    position: relative;
    width: 100%;
    height: auto;
    padding: 0 0 30px;
  }
}

.main-nav__item {
  margin: 0 68px 10px 67px;

  @media (min-width: $mobile-width) and (max-width: $tablet-max-width) {
    margin: 0 0 30px;
  }
}

.main-nav__link {
  display: block;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.01px;
  color: $basic__white;
  opacity: 0.7;

  &:hover,
  &:focus {
    color: $basic__accent;
    outline: none;
    opacity: 1;
  }

  @media (min-width: $mobile-width) and (max-width: $tablet-max-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: auto;
    min-width: 320px;
    height: 60px;
    padding: 0 16px;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: $basic__dark-four;
    background-color: $basic__light-four;
    opacity: 1;
    box-sizing: border-box;

    &::after {
      content: "";
      width: 25px;
      height: 25px;
      background-image: url("../img/icons/arrow-icon.svg");
      background-repeat: no-repeat;
    }
  }
}
