@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: #000000;
}

.mobile-menu {
  overflow: hidden;
}

.page-header {
  width: 100%;
  margin: 0 auto;
  padding: 21px 0 239px;
  background-color: #131314;
  background-image: url("../img/bike.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
}
@media (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/bike@2x.jpg");
  }
}
@media (min-width: 0) and (max-width: 1023px) {
  .page-header {
    padding: 0;
    background-color: transparent;
    background-image: linear-gradient(transparent 340px, #ffffff 340px), url("../img/bike-tablet.jpg"), linear-gradient(#131314 340px, transparent 340px);
    background-size: 100%;
    background-position: top;
  }
}
@media (min-width: 0) and (max-width: 1023px) and (min-resolution: 2dppx) {
  .page-header {
    background-image: linear-gradient(transparent 340px, #ffffff 340px), url("../img/bike-tablet@2x.jpg"), linear-gradient(#131314 340px, transparent 340px);
  }
}
@media (min-width: 0) and (max-width: 767px) {
  .page-header {
    background-image: linear-gradient(transparent 390px, #ffffff 390px), url("../img/bike-mobile.jpg"), linear-gradient(#131314 390px, transparent 390px);
  }
}
@media (min-width: 0) and (max-width: 767px) and (min-resolution: 2dppx) {
  .page-header {
    background-image: linear-gradient(transparent 390px, #ffffff 390px), url("../img/bike-mobile@2x.jpg"), linear-gradient(#131314 390px, transparent 390px);
  }
}

.page-header__wrapper {
  position: relative;
  width: 880px;
  margin: 0 auto;
}
@media (min-width: 0) and (max-width: 767px) {
  .page-header__wrapper {
    width: auto;
    min-width: 320px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .page-header__wrapper {
    width: auto;
    min-width: 768px;
  }
}

.page-header__logo {
  display: block;
  position: absolute;
  top: 297px;
  left: 0;
  width: 68px;
  height: 21px;
}
@media (min-width: 0) and (max-width: 1023px) {
  .page-header__logo {
    position: relative;
    top: 25px;
    left: 22px;
  }
}
@media (min-width: 0) and (max-width: 767px) {
  .page-header__logo {
    left: 17px;
  }
}

.page-header__logo img {
  display: block;
}

.nav-toggle {
  display: none;
}
@media (min-width: 0) and (max-width: 1023px) {
  .nav-toggle {
    display: block;
    padding: 0;
    cursor: pointer;
  }
}

@media (min-width: 0) {
  .nav-toggle--closed {
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    background-color: #0ad9c6;
    border: none;
  }
}

@media (min-width: 0) and (max-width: 1023px) {
  .nav-toggle--opened {
    position: fixed;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    padding: 0;
    background: url("../img/icons/cross-icon.svg") no-repeat 50%;
    border: none;
    z-index: 150;
  }
}

.nav-toggle--nojs {
  display: none;
}

@media (min-width: 0) and (max-width: 1023px) {
  .nav-toggle__button {
    position: relative;
    display: block;
    margin: 0 auto;
    width: 22px;
    height: 13px;
    border-top: 1px solid #000000;
    border-bottom: 1px solid #000000;
    box-sizing: border-box;
  }
  .nav-toggle__button::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #000000;
  }
}

@media (min-width: 0) and (max-width: 1023px) {
  .nav-toggle--opened .nav-toggle__button {
    display: none;
  }
}

.main-nav {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 auto 260px;
  padding-left: 4px;
}
@media (min-width: 0) and (max-width: 1023px) {
  .main-nav {
    order: 2;
  }
  .main-nav--closed {
    display: none;
  }
  .main-nav--opened {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0;
    z-index: 100;
  }
  .main-nav--nojs {
    display: block;
    width: 100%;
    margin: 0 0 30px;
    padding: 0;
  }
}

.main-nav__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 1009px;
  margin: 0 -68px 0 -67px;
  padding: 0 0 0 17px;
  list-style: none;
  box-sizing: border-box;
}
.main-nav__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 66px;
  width: 874px;
  height: 1px;
  background-color: #ffffff;
  opacity: 0.2;
}
@media (min-width: 0) and (max-width: 1023px) {
  .main-nav__list {
    flex-wrap: nowrap;
    flex-direction: column;
    width: auto;
    min-width: 320px;
    height: 100vh;
    margin: 0;
    padding: 100px 0;
    background-color: #ffffff;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
  .main-nav__list::after {
    display: none;
  }
}

@media (min-width: 0) and (max-width: 1023px) {
  .main-nav--nojs .main-nav__list {
    position: relative;
    width: 100%;
    height: auto;
    padding: 0 0 30px;
  }
}

.main-nav__item {
  margin: 0 68px 10px 67px;
}
@media (min-width: 0) and (max-width: 1023px) {
  .main-nav__item {
    margin: 0 0 30px;
  }
}

.main-nav__link {
  display: block;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.01px;
  color: #ffffff;
  opacity: 0.7;
}
.main-nav__link:hover, .main-nav__link:focus {
  color: #0ad9c6;
  outline: none;
  opacity: 1;
}
@media (min-width: 0) and (max-width: 1023px) {
  .main-nav__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: auto;
    min-width: 320px;
    height: 60px;
    padding: 0 16px;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #48494d;
    background-color: #f6f6f6;
    opacity: 1;
    box-sizing: border-box;
  }
  .main-nav__link::after {
    content: "";
    width: 25px;
    height: 25px;
    background-image: url("../img/icons/arrow-icon.svg");
    background-repeat: no-repeat;
  }
}

.page-header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1009px;
  margin: 0 auto;
}
@media (min-width: 0) and (max-width: 1023px) {
  .page-header__container {
    flex-direction: column;
    width: 100%;
    align-items: center;
  }
}

.main-block {
  width: 381px;
  margin-left: 65px;
  padding: 46px 0 0;
  letter-spacing: -0.1px;
  color: #ffffff;
}
@media (min-width: 0) and (max-width: 767px) {
  .main-block {
    order: 1;
    width: 288px;
    margin: 0 0 140px;
    padding: 100px 0 0;
    text-align: center;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .main-block {
    order: 1;
    width: 435px;
    margin: 0 0 150px;
    padding: 114px 0 0;
    text-align: center;
  }
}

.main-block h2 {
  margin: 0 0 25px;
  padding: 0;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  letter-spacing: -0.25px;
}
@media (min-width: 0) and (max-width: 1023px) {
  .main-block h2 {
    margin: 0 0 19px;
    font-size: 30px;
    line-height: 42px;
    letter-spacing: -0.15px;
  }
}

.main-block p {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  opacity: 0.8;
}
@media (min-width: 0) and (max-width: 1023px) {
  .main-block p {
    font-size: 18px;
    letter-spacing: -0.08px;
  }
}

.selection-form {
  width: 327px;
  margin-right: 65px;
}
@media (min-width: 0) and (max-width: 767px) {
  .selection-form {
    order: 3;
    width: 100%;
    margin: 0 auto;
    padding: 0 16px 71px;
    box-sizing: border-box;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .selection-form {
    order: 3;
    width: 321px;
    margin-right: 0;
    padding: 0 0 70px;
  }
}

.selection-form__description {
  margin: 0 0 19px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  letter-spacing: -0.01px;
  color: #ffffff;
  text-align: center;
}
@media (min-width: 0) and (max-width: 1023px) {
  .selection-form__description {
    margin: 0 0 30px;
    font-size: 18px;
    letter-spacing: -0.08px;
    color: #000000;
  }
}
@media (min-width: 0) and (max-width: 767px) {
  .selection-form__description {
    margin: 0 0 20px;
  }
}

.selection-form input {
  width: 100%;
  height: 48px;
  margin: 0 0 20px;
  padding: 3px 28px 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #1c1c1c;
  background-color: #ffffff;
  border: 1px solid #c9ccd4;
  border-radius: 6px;
  box-sizing: border-box;
}
.selection-form input::placeholder {
  opacity: 0.5;
}
.selection-form input:hover, .selection-form input:focus {
  padding: 3px 26px 0;
  border: 3px solid #0ad9c6;
  outline: none;
}
@media (min-width: 0) and (max-width: 767px) {
  .selection-form input {
    padding: 2px 25px 0;
  }
  .selection-form input:hover, .selection-form input:focus {
    padding: 2px 23px 0;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .selection-form input {
    padding: 0 20px;
  }
  .selection-form input:hover, .selection-form input:focus {
    padding: 0 18px;
  }
}

.selection-form__button {
  width: 100%;
  height: 51px;
  padding: 2px 0 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  border: none;
  background-color: #0ad9c6;
  border-radius: 6px;
}
.selection-form__button:hover, .selection-form__button:focus {
  border: 2px solid #ffffff;
  outline: none;
}
.selection-form__button:active {
  border: 2px solid #ffffff;
  color: #ffffff;
}
@media (min-width: 0) and (max-width: 767px) {
  .selection-form__button {
    margin: -1px 0 0;
    padding: 0 2px 3px 0;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .selection-form__button {
    padding: 2px 0 0 2px;
  }
}

.selection-form .invalid {
  padding: 3px 26px 0;
  border: 3px solid #fe7865;
  outline: none;
}

.success {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 728px;
  height: 444px;
  margin: auto;
  padding: 179px 0;
  background-color: #dadada;
  box-sizing: border-box;
  z-index: 1000;
}
.success--open {
  display: block;
}
@media (min-width: 0) and (max-width: 767px) {
  .success {
    width: auto;
    max-width: 728px;
    height: auto;
    max-height: 444px;
    padding: 179px 16px;
  }
}

.success p {
  margin: 0 auto;
  max-width: 399px;
  font-weight: 500;
  font-size: 25px;
  line-height: 43px;
  color: #1c1c1c;
  text-align: center;
}

.success__button {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 22px;
  height: 22px;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/icons/cross-icon.svg");
  border: 0;
  cursor: pointer;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.8;
  z-index: 100;
}
.overlay--open {
  display: block;
}

.facts {
  margin: 0 auto;
  padding: 151px 0 153px;
  text-align: center;
}
@media (min-width: 0) and (max-width: 767px) {
  .facts {
    padding: 0 15px 81px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .facts {
    padding: 0 106px 70px;
  }
}

.facts h2 {
  margin: 0 0 49px;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.08px;
  color: #1f1f1f;
}
@media (min-width: 0) and (max-width: 1023px) {
  .facts h2 {
    margin: 0 0 30px;
    font-size: 24px;
    line-height: 41px;
    letter-spacing: -0.03px;
  }
}

.facts__list {
  counter-reset: section;
  display: grid;
  grid-template-columns: repeat(4, 263px);
  column-gap: 38px;
  row-gap: 43px;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 1024px) and (max-width: 1200px) {
  .facts__list {
    grid-template-columns: repeat(2, 263px);
  }
}
@media (min-width: 0) and (max-width: 767px) {
  .facts__list {
    grid-template-columns: 263px;
    row-gap: 46px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .facts__list {
    grid-template-columns: repeat(2, 263px);
    column-gap: 30px;
  }
}

.facts__item {
  width: 263px;
}
.facts__item::before {
  counter-increment: section;
  content: counter(section, decimal-leading-zero);
  display: block;
  margin: 0 0 14px;
  padding-right: 2px;
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: -1.27px;
  color: #0ad9c6;
}
@media (min-width: 0) and (max-width: 767px) {
  .facts__item::before {
    margin: 0 0 8px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .facts__item::before {
    margin: 0 0 15px;
  }
}

.fact p {
  margin: 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  color: #000000;
}
@media (min-width: 0) and (max-width: 767px) {
  .fact p {
    font-size: 18px;
  }
}

.bicycles {
  margin: 0 auto;
  padding: 150px 80px 100px;
  text-align: center;
  background-color: #f6f7f9;
}
@media (min-width: 0) and (max-width: 767px) {
  .bicycles {
    padding: 50px 25px 12px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .bicycles {
    padding: 50px 52px 20px;
  }
}

.bicycles h2 {
  margin: 0 0 50px;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.08px;
  color: #1f1f1f;
}
@media (min-width: 0) and (max-width: 1023px) {
  .bicycles h2 {
    margin: 0 0 30px;
    font-size: 24px;
    line-height: 41px;
  }
}

.bicycles__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  width: 1175px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
@media (min-width: 1024px) and (max-width: 1340px) {
  .bicycles__list {
    width: 790px;
  }
}
@media (min-width: 0) and (max-width: 767px) {
  .bicycles__list {
    width: 270px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .bicycles__list {
    width: 640px;
  }
}

.bicycle {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  margin: 0 auto 50px;
}
@media (min-width: 0) and (max-width: 767px) {
  .bicycle {
    margin: 0 auto 42px;
  }
}

.bicycle--road {
  margin-top: 10px;
}
.bicycle--road::before {
  content: "";
  position: absolute;
  top: 156px;
  left: -60px;
  width: 180px;
  height: 180px;
  border: 27px solid #0ad9c6;
  border-radius: 50%;
  box-sizing: border-box;
  z-index: 0;
}
@media (min-width: 1024px) and (max-width: 1340px) {
  .bicycle--road {
    margin-top: 0;
  }
}
@media (min-width: 0) and (max-width: 1023px) {
  .bicycle--road {
    margin-top: 0;
  }
  .bicycle--road::before {
    top: auto;
    bottom: 26px;
    left: -37px;
    width: 127px;
    height: 127px;
    border-width: 18px;
  }
}
@media (min-width: 0) and (max-width: 639px) {
  .bicycle--road::before {
    bottom: 26px;
    left: -24px;
  }
}

.bicycle--mountain {
  margin-top: 39px;
}
.bicycle--mountain::before, .bicycle--mountain::after {
  content: "";
  position: absolute;
  top: -18px;
  left: -19px;
  width: 47px;
  height: 47px;
  border: 8px solid #0ad9c6;
  box-sizing: border-box;
  z-index: 10;
}
.bicycle--mountain::after {
  top: auto;
  bottom: 31px;
  left: auto;
  right: -23px;
}
@media (min-width: 1024px) and (max-width: 1340px) {
  .bicycle--mountain {
    margin-top: 0;
  }
}
@media (min-width: 0) and (max-width: 1023px) {
  .bicycle--mountain {
    margin-top: 0;
  }
  .bicycle--mountain::before, .bicycle--mountain::after {
    display: none;
  }
}

.bicycle--children::before {
  content: "";
  position: absolute;
  top: -32px;
  right: -36px;
  width: 95px;
  height: 95px;
  border: 17px solid #0ad9c6;
  border-radius: 50%;
  box-sizing: border-box;
  z-index: 10;
}
@media (min-width: 0) and (max-width: 1023px) {
  .bicycle--children::before {
    top: -20px;
    right: -25px;
    width: 68px;
