  }
}
@media (min-width: 0) and (max-width: 639px) {
  .bicycle--children::before {
    right: -15px;
  }
}

.bicycle h3 {
  margin: 0;
  font-weight: 600;
  font-size: 22px;
  letter-spacing: -0.5px;
  color: #000000;
}

.bicycle img {
  position: relative;
  display: block;
  margin-bottom: 20px;
  border-radius: 10px;
  box-sizing: border-box;
  z-index: 5;
}
@media (min-width: 0) and (max-width: 1023px) {
  .bicycle img {
    width: 270px;
    height: 300px;
  }
}

@media (min-width: 0) and (max-width: 1023px) {
  .bicycle--road img {
    content: url("../img/road-bike-mini.jpg");
  }
}
@media (min-width: 0) and (max-width: 1023px) and (min-resolution: 2dppx) {
  .bicycle--road img {
    content: url("../img/road-bike-mini@2x.jpg");
  }
}

@media (min-width: 1024px) and (max-width: 1340px) {
  .bicycle--mountain img {
    height: 503px;
  }
